<template>
    <div class="header">
        <van-icon name="arrow-left" class="icon" @click="toBack" />
        <div>{{ title }}</div>
        <div class="edit" @click="editClick" v-if="edit">
        {{ store.state.edit ? "编辑" : "完成" }}
        </div>
    </div>
</template>
<script >
import emitter from "@/common/js/evenbus"
import { showToast } from "vant"
import { useStore } from "vuex"
import { useRouter } from "vue-router"
export default {
    props: ["title", "edit"],
    setup() {
        const store = useStore()
        const router = useRouter()
        const toBack = () => {
            router.back()
        }

        const editClick = () => {
            if (store.state.cartList.length) {
                store.commit("EDIT")
                emitter.emit("edit")
            } else {
                showToast({
                    message: '购物车中没有商品',
                    icon: 'like-o',
                })
            }
        }

        return {
            editClick,
            toBack,
            store
        }
    }

}
</script>
<style scoped lang="less">
.header {
    background-color: #fff;
    height: 40px;
    font-size: 20px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-bottom: 1px solid #d7d7d7;

    .icon {
        position: absolute;
        left: 10px;
    }

    .edit{
        position: absolute;
        right:  10px; 
    }
}
</style>