<template>
    <div class="login">
        <Header title="登录" />
        <div class="img">某团</div>
        <van-form @submit="onSubmit">
            <van-cell-group inset>
                <van-field
                    v-model="username"
                    name="用户名"
                    label="用户名"
                    placeholder="用户名"
                    :rules="[{ required: true, message: '请填写用户名' }]"
                />
                <van-field
                v-model="password"
                type="password"
                name="密码"
                label="密码"
                placeholder="密码"
                :rules="[{ required: true, message: '请填写密码' }]"
                />
            </van-cell-group>
            <div style="margin: 16px;">
                <van-button round block type="primary" native-type="submit">
                登录
                </van-button>
                <van-button round block type="primary" @click="toRegister" class="register">
                注册
                </van-button>
            </div>
        </van-form>
    </div>
</template>
<script>
import Header from "@/components/Header.vue"
import { reactive, toRefs } from "vue"
import { useRouter } from "vue-router"
import { showToast } from 'vant';
export default {
    components: {
        Header
    },
    setup(){
        let router = useRouter()
        let data=reactive({
            username:"",
            password:""
        })


        let onSubmit=(value)=>{
            if (!localStorage.userInfo) {
                showToast("没有注册的用户")
                return
            } else {
                let userInfo = JSON.parse(localStorage.userInfo)
                if (userInfo["用户名"] === value["用户名"]) {
                    if(userInfo["密码"] === value["密码"]){
                        showToast("登录成功")
                        localStorage.setItem("isLogin","1")
                        router.push("/home")
                    }else{
                        showToast("用户名、密码错误")
                    }
                } else {
                    showToast("用户名、密码错误")
                }
            }
        }

        let toRegister=()=>{
            router.push("/register")
        }


        return {
            ...toRefs(data),
            onSubmit,
            toRegister
        }
    }
}
</script>
<style scoped lang="less">
.login{
    .img{
        width: 200px;
        height: 200px;
        background-color: #ffc400;
        font-size: 80px;
        line-height: 200px;
        text-align: center;
        border-radius: 40px;
        margin: 20px auto;
    }
    .register{
        margin-top: 20px;
    }
}
</style>